

.homeRightBox {
    width: 100%;
    background: #1F1D2B;
    height: 100%;
    padding: 24px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    &-header {
        padding-bottom: 24px;
        border-bottom: 1px solid #393C49;
        .title {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 140%;
            color: #FFFFFF;
            margin-bottom: 24px;
        }

        &-btnWrapper {
            display: flex;
            align-items: center;
            margin-bottom: 24px;

            button {
                background: transparent;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                padding: 7px 12px;
                margin-right: 8px;
                outline: none;
                color: #EA7C69;

                &:hover {
                    color: #FFFFFF;
                    background: #EA7C69;
                    border: 1px solid transparent;
                }
            }
        }

        &-title {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;

            &-right {
                span {
                    margin-left: 43px;
                }
            }


            span {
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 140%;
                color: #FFFFFF;
            }
        }
    }

    &-body {
        
        overflow-y: scroll;
        height: 50vh;

        &::-webkit-scrollbar {
            width: 0 !important;
            height: 0;
            display: none !important;
            background: transparent;
        }
    }

    &-footer {
        height: 20vh;
        padding-top: 24px;
        box-sizing: border-box;
        width: 100%;
        border-top: 1px solid #393C49;
        span {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #ABBBC2;
        }

        h3 {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            text-align: right;
            color: #FFFFFF;
        }



        &-descount {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 19px;
        }   

        &-total {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 19px;
        }

        &-btn {
            width: 100%;

            button {
                width: 100%;
                height: 48px;
                outline: none;
                border: none;
                background: #EA7C69;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
                border-radius: 8px;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FAFAFA;
            }

        }
    }
}