.progresRightBox {
    padding: 24px;
    box-sizing: border-box;
    height: 100%;
    &-top {
        background: #1F1D2B;
        border-radius: 8px;
        padding: 24px;
        box-sizing: border-box;

        &-header{
            padding-bottom: 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 2px solid #393C49;
            margin-bottom: 20px;
            h2 {
                font-style: normal;
                font-weight: 600;
                font-size: 20px;
                line-height: 140%;
                color: #FFFFFF;
            }

            select {
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                height: 48px;
                padding-left: 14px;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                color: #E0E6E9;
                outline: none;
                margin-top: 8px;
            }

        }

        &-footer {
            button {
                cursor: pointer;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #EA7C69;
                background: transparent;
                width: 100%;
                height: 48px;
                border: 1px solid #EA7C69;
                box-sizing: border-box;
                border-radius: 8px;

                &:hover {
                    color: white;
                    background: #EA7C69;
                }
            }
        }

    }

    &-bottom {
        margin-top: 24px;
        background: #1F1D2B;
        border-radius: 8px;
        padding: 24px;


        &-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 24px;
            padding-bottom: 24px;
            box-sizing: border-box;
            border-bottom: 2px solid #393C49;



            h2 {
                font-style: normal;
                font-weight: 600;
                font-size: 20px;
                line-height: 140%;
                color: #FFFFFF;
            }

            select {
                 background: #2D303E;
                 border: 1px solid #393C49;
                 box-sizing: border-box;
                 border-radius: 8px;
                 height: 48px;
                 padding-left: 14px;
                 font-style: normal;
                 font-weight: normal;
                 font-size: 14px;
                 line-height: 140%;
                 color: #E0E6E9;
                 outline: none;
                 margin-top: 8px;
            }
        }

        &-body {
            display: flex;
            align-items: center;


            &-left {
                
            }

            &-right {
                margin-left: 40px;


                h5 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    display: flex;
                    align-items: center;
                    color: #FFFFFF;
                }

                span {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 12px;
                    line-height: 140%;
                    display: flex;
                    align-items: center;
                    color: #ABBBC2;

                }

                .info {

                }

                .line {
                    display: flex;
                    position: relative;
                    margin-bottom: 16px;
                    
                    .round {
                        background: #FF7CA3;
                        position: absolute;
                        width: 15px;
                        height: 15px;
                        border-radius: 50%;
                        left: -20px;
                    }
                } 
            }
        }
    }
}