.homeZakaz {
    width: 100%;
    margin-bottom: 28px;

    &-row1 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;

        &-left {
            width: 88%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            &-info {
                display: flex;
                align-items: center;

                &-img {
                    width: 45px;
                    height: 45px;
                    border-radius: 50%;
                    margin-right: 5px;
                    img {
                        width: 45px;
                        height: 45px;
                        border-radius: 50%;
                    }    
                }
            }
        }

        &-right {
            width: 12%;
        }

        input {
            width: 48px;
            height: 48px;
            background: #2D303E;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            outline: none;
            border: none;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #FFFFFF;
            text-align: center;
        }

        h4 {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            text-align: right;
            color: #FFFFFF;
        }

        h5 {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #FFFFFF;
        }

        span {
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }

    &-row2 {
        
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        &-left {
            width: 88%;
            input {
                height: 48px;
                width: 100%;
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                color: #889898;
                padding-left: 38px;
                box-sizing: border-box;
                outline: none;
            }
        }

        &-right {
            width: 12%;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            button {
                border: 1px solid #EA7C69;
                box-sizing: border-box;
                border-radius: 8px;
                width: 48px;
                height: 48px;
                background: transparent;
                display: flex;
                align-items: center;
                justify-content: center;
                outline: none;
                cursor: pointer;

                svg {
                 fill: #EA7C69;
                }

                &:hover {
                    border: 1px solid #FF7CA3;

                    svg {
                        fill: #FF7CA3;
                    }
                }
            }
        }
    }

}