.settingBlock {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 221px;
    background: #1F1D2B;
    border: 1px solid #393C49;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 16px;

    &-body {
        display: flex;
        align-items: center;
        flex-direction: column;
        text-align: center;
        margin-bottom: 16px;
        

        &-img {
            padding: 20px;
            box-sizing: border-box;

            img {
                width: 140px;
                height: 140px;
                border-radius: 50%;
            }
        }

        h4 {
            padding: 0 38px;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            text-align: center;
            color: #FFFFFF;
            margin-bottom: 8px;
        }
        span {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: #ABBBC2;

        }
    }

    button {
        width: 100%;
        display: flex;
        border: none;
        height: 48px;
        color: #EA7C69;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #ea7c6950;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        
        svg {
            margin-right: 10px;
            fill: #EA7C69;
        }
    }
}