.homeRightBox1 {
    width: 100%;
    overflow: hidden;
    height: 100%;
    background: #1F1D2B;
    padding: 24px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    position: relative;

    

    &-header {
        border-bottom: 2px solid #393C49;
        padding-bottom: 24px;
        h2 {
            font-style: normal;
            font-weight: 600;
            font-size: 28px;
            line-height: 140%;
            color: #FFFFFF;
        }

        span {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }

    &-body {
        padding-top: 24px;


        h3 {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 140%;
            color: #FFFFFF;
            margin-bottom: 16px;
        }

        &-wrapperCard {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 16px;
            

            .ptichka {
                position: absolute;
                right: 6px;
                top: 6px;
                display: none;
            }   

            label {
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                flex-direction: column;
                position: relative;
                width: 100px;
                height: 64px;
                border: 1px solid #ABBBC2;
                padding: 14px;
                border-radius: 8px;
                color: #ABBBC2;
                cursor: pointer;
                

                svg {
                    fill: #ABBBC2;
                }
            }

            input {
                display: none;

                &:checked + label {
                    border: 1px solid #fff;
                    color: #fff;

                    .ptichka {
                        display: flex;
                    }

                    .icon {
                        svg {
                            fill: white;
                        }
                    } 
                }
            }
        }

        .row1, .row2  {
            margin-top: 8px;
            display: block;
            label {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                margin-top: 8px;
                
            }

            input {
                width: 100%;
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                height: 48px;
                padding-left: 14px;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                color: #E0E6E9;
                outline: none;
                margin-top: 8px;

                &::placeholder {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                }
            }
        }

        .row3, .row4 {
            margin-top: 8px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            &-right {
                display: flex;
                flex-direction: column;
                width: 45%;
            }

            &-left {
                display: flex;
                flex-direction: column;
                width: 45%;
            }

            .parol {
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 36px;
                width: 100%;
                &::placeholder {
                    font-size: 18px;
                }
            }

            label {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                margin-top: 8px;


            }

            input {
                width: 100%;
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                height: 48px;
                padding-left: 14px;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                color: #E0E6E9;
                outline: none;
                margin-top: 8px;

                &::placeholder {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                }
            }

            select {
                width: 100%;
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                height: 48px;
                padding-left: 14px;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 140%;
                color: #E0E6E9;
                outline: none;
                margin-top: 8px;
            }
        } 


        .footer {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 150px;
            
            
            button {
                width: 45%;
                outline: none;
                padding: 14px 34px;
                cursor: pointer;    
                
                

                &:first-child {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #EA7C69;
                    background: transparent;
                    border: 1px solid #EA7C69;
                    border-radius: 8px;
                }

                &:last-child {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #FAFAFA;
                    background: #EA7C69;
                    box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
                    border-radius: 8px;
                    border: transparent;
                }
            }
        }

        
    }
}