.settingsRight {
    background: #1F1D2B;
    padding: 24px;
    width: 100%;
    box-sizing: border-box;
    border-radius: 8px;
    
    &-header {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        h2 {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 140%;
            color: #FFFFFF;
        }

        &-filter {

            display: flex;
            align-items: center;
            background: #1F1D2B;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 15px;
            cursor: pointer;

            

            h3 {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                margin-left: 10px;
            }
        }
    }

    &-body {
        


        &-top {
            display: flex;
            align-items: center;
            margin-bottom: 24px;
            border-bottom: 2px solid #393C49;


            a {
                padding: 13px 32px 13px 0;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FFFFFF;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    width: 0%;
                    height: 3px;
                    background: #EA7C69;
                    bottom: -2.5px;
                    border-radius: 30px;
                }

                &:hover {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #EA7C69;

                    &:before {
                        width: 50%;
                    }

                }
            }
        }

        &-bottom {

            &::-webkit-scrollbar {
                width: 0 !important;
                height: 0;
                display: none !important;
                background: transparent;
            }

            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            flex-wrap: wrap;
            height: 54vh;
            overflow-y: scroll;

            &-addBtn {
                display: flex;
                flex-direction: column;
                width: 221px;
                height: 310px;
                justify-content: center;
                align-items: center;
                border-radius: 8px;
                border: 2px dashed #EA7C69;

                h4 {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 140%;
                    text-align: center;
                    color: #EA7C69;
                    margin-top: 10px;
                }
            }
        }
    }

    &-footer {
        margin-top: 50px;


        button {


            width: 300px;
            outline: none;
            padding: 14px 34px;
            cursor: pointer;



            &:first-child {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #EA7C69;
                background: transparent;
                border: 1px solid #EA7C69;
                border-radius: 8px;
            }

            &:last-child {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FAFAFA;
                background: #EA7C69;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
                border-radius: 8px;
                border: transparent;
                margin-left: 10px;
            }
        }
    }
}