.sidebar {
    width: 104px;
    height: 100vh;
    background: #1F1D2B;
    border-radius: 0 16px 16px 0;

    &-header {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 24px;

        .logo {
            border: 11px solid transparent;
            border-radius: 12px;
            background: #eb956a41;
        }
    }
}

.sidebar-btnWrapper {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    


    &-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 90px;
        height: 90px;
        border-radius: 12px 0 0 12px;

        &.active {
            background: #252836;
            

            .sidebarBtn {
                background: #EA7C69;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
                svg {
                    fill: white;
                }


            }
        }
    }
}