

.progressBlock {
    width: 30%;
    background: #1F1D2B;
    border-radius: 8px;
    padding: 16px;
    box-sizing: border-box;

    .row1 {
        display: flex;
        align-items: center;
        margin-bottom: 8px;

        &-icon {
            background: #252836;
            border-radius: 8px;
            height: 38px;
            width: 38px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &-info {
            display: flex;
            align-items: center;
            margin-left: 12px;

            &-num {
                font-style: normal;
                font-weight: 500;
                font-size: 12px;
                line-height: 140%;
                color: #50D1AA;
            }

            &-arrow {
                background: rgba(0, 208, 0, 0.143);
                width: 10px;
                height: 10px;
                border-radius: 50%;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 6px;

                
            }
        }
    }

    .row2 {
        margin-bottom: 8px;
        h2 {
            font-style: normal;
            font-weight: 600;
            font-size: 28px;
            line-height: 140%;
            color: #FFFFFF;
        }
    }

    .row3 {
        span {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #ABBBC2;
        }
    }
}