body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #252836;
}

h1, h2, h3, h4, h5, h6, p, a {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

.body {
    display: flex;
    
    &-right{
        width: 100%;
        height: 100vh;
        padding: 0 0 0  24px;
        box-sizing: border-box;
    }
}