.peopleInfo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 24px;

    .colum {
        display: flex;
        align-items: center;

        .peopleImg {
            display: flex;
            align-items: center;
            justify-content: center;
            background: #FFB572;
            margin-right: 16px;
            border-radius: 50%;
        }

        h3 {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #E0E6E9;
        }

        &:nth-child(1) {
            width: 30%;
        }

        &:nth-child(2) {
            width: 30%;
            h3 {
                width: 50%;
            }
        }

        &:nth-child(3) {
            width: 25%;
        }

        &:nth-child(4) {
            width: 15%;
            span {
                padding: 4px 12px;
                border-radius: 30px;
            }
        }
    }
}