.dishBlock {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 24px;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    width: 192px;
    height: 226px;
    background: #1F1D2B;
    margin-bottom: 70px;
    border-radius: 16px;
    position: relative;
    cursor: pointer;

    &-img {
        width: 145px;
        height: 145px;
        position: relative;
        position: absolute;
        top: -50px;

        img {
            width: 145px;
            height: 145px;
            border-radius: 50%;
        }
    }

    &-info {
        text-align: center;
        position: absolute;
        bottom: 24px;

        h4 {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            text-align: center;
            color: #FFFFFF;
            padding: 0 24px;
            margin-bottom: 8px;
        }

        h3 {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: #FFFFFF;
            margin-bottom: 4px;
        }

        span {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: #ABBBC2;
        }

    }

}