

.progresPeople-inner {
    padding: 20px 24px 0 24px;
    box-sizing: border-box;
    background: #1F1D2B;
    border-radius: 8px;
    height: 500px;
    
    



    &-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        h2 {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 140%;
            color: #FFFFFF;
        }

        .filter {

            display: flex;
            align-items: center;
            background: #1F1D2B;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 15px;
            cursor: pointer;
            &-icon{

            }

            h4 {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                margin-left: 10px;
            }

        }
    }

    &-body {
        margin-top: 24px;
        

        &-title {
            border-bottom: 2px solid #393C49;
            padding-bottom: 16px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;


            h3 {
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FFFFFF;

                &:nth-child(1) {
                    width: 30%;
                }
                &:nth-child(2) {
                    width: 30%;
                }
                &:nth-child(3) {
                    width: 25%;
                }
                &:nth-child(4) {
                    width: 15%;
                }
                
            }
        }

        &-box{
            margin-top: 32px;
            overflow-y: scroll;
            height: 330px;

            &::-webkit-scrollbar {
                width: 0 !important;
                height: 0;
                display: none !important;
                background: transparent;
            }
            
        }
    }
}