.home {
    display: flex;
    height: 100vh;
    justify-content: space-between;
    width: 100%;

    .modal {
        position: fixed;
        margin: 0 auto;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        transform: scale(0);
        height: 100vh;
        &-left {
            width: 60%;
            background: rgba(0, 0, 0, 0.394);
            height: 100vh;

        }

        &-right {
            width: 40%;
            height: 100vh;

        }

        &.active {
            transform: scale(1);
        }

    }

    &-left {
        width: 60%;
        padding: 24px 24px 0 0;

        &-header {
            &-row1 {
                display: flex;
                align-items: center;
                justify-content: space-between;
                h1 {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 28px;
                    line-height: 140%;
                    color: #FFFFFF;
                }

                span {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 140%;
                    color: #E0E6E9;

                }

                .inputWrapper {
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    display: flex;
                    align-items: center;
                    height: 48px;
                    width: 220px;
                    padding: 15px;
                    box-sizing: border-box;

                    .inputImg {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }

                    input {
                        margin-left: 8px;
                        border: none;
                        outline: none;
                        background: transparent;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 140%;
                        color: #ABBBC2;
                        width: 100%;
                    }

                }
            }

            &-btnWrapper {
                display: flex;
                align-items: center;
                margin-bottom: 24px;
                border-bottom: 2px solid #393C49;
                

                a {
                    padding: 13px 32px 13px 0;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #FFFFFF;
                    position: relative;

                    &::before {
                        content: "";
                        position: absolute;
                        width: 0%;
                        height: 3px;
                        background: #EA7C69;
                        bottom: -2.5px;
                        border-radius: 30px;
                    }

                    &:hover {
                        font-style: normal;
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 140%;
                        color: #EA7C69;

                        &:before {
                            width: 50%;
                        }

                    }
                }
            }
        }

        &-body {
            overflow: hidden    ;
            &-row1 {
                display: flex;
                justify-content: space-between;

                h2 {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 140%;
                    color: #FFFFFF;
                }

                select {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #FFFFFF;
                    background: #1F1D2B;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    height: 48px;
                    padding: 10px;
                    box-sizing: border-box;
                    outline: none;
                    

                    
                }   
            }

            &-row2 {
                padding: 50px 0 0 0 ;
                box-sizing: border-box;
                overflow-y: scroll;
                display: flex;
                align-items: center;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-top: 24px;
                height: 70vh;
                &::-webkit-scrollbar {
                    width: 0 !important;
                    height: 0;
                    display: none !important;
                    background: transparent;
                } 
            }
        }
    }

    &-right {
        width: 40%;
    }
}

.progres {
    display: flex;

    height: 90%;

    


    &-body {
        padding: 24px 24px 0 0;
        height: 100%;
        

        &-header {
            margin-bottom: 24px;
            padding-bottom: 24px;
            border-bottom: 2px solid #393C49;
            h1 {
                font-style: normal;
                font-weight: 600;
                font-size: 28px;
                line-height: 140%;
                color: #FFFFFF;
            }

            span {
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 140%;
                color: #ABBBC2;
            }
        }
        width: 60%;

        &-blocksWrapper {
            display: flex;
            justify-content: space-between;
        }
    }

    .progresPeople {
        margin-top: 24px;
        height: 70%;
        
    }

    &-right {
        width: 40%;
    }
}

.settings {
    width: 100%;
    height: 100vh;
    padding-top: 24px;
    box-sizing: border-box;

    &-header {
        color: white;
        margin-bottom: 24px;
    }
        

    &-body {
        display: flex;
        width: 100%;
        
        
    }

    &-body-left {
        height: 100%;
        width: 25%;
        
    }

    &-body-right {
        width: 65%;
        height: 100%;
    }
}