.settingsLeftBlock {
    display: flex;
    background: #1F1D2B;
    padding: 24px;
    cursor: pointer;position: relative;

    &::before {
        content: "";
        position: absolute;
        width: 4px;
        height: 40px;
        background: #EA7C69;
        right: 0;
        border-radius: 30px;
        display: none;
    }


    &:hover {
        background: rgba(234, 124, 105, 0.26);

        &::before {
            display: flex;
        }

        svg {
            fill: #EA7C69;
        }

        h4 {
            color: #EA7C69;
        }

        span {
            color: #ABBBC2;
        }
    }

    &-icon {
        svg {
            fill: #ABBBC2;
            width: 13px;
            height: 13px;
        }
    }

    &-info {
        margin-left: 10px;
        
            h4 {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
            }

            span {
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 140%;
                color: #ABBBC2;
            }
    }
}