

.mostOrdered {
    display: flex;
    align-items: center;
    margin-bottom: 24px;


    &-img {
        overflow: hidden;
        width: 60px;
        height: 60px;


        img {
            border-radius: 50%;
            width: 60px;
            height: 60px;
        }
    }

    &-info {
        margin-left: 13px;
        h4 {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #E0E6E9;
        }

        span {
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }
}